<template>
    <div class="admin-portal" style="position: relative !important;top: 180px !important;">
        <h2>Admin Portal - Data Zilizotumwa</h2>

        <!-- Table to display the submissions -->
        <a-table :columns="columns" :data-source="submissions" row-key="id" bordered :pagination="paginationConfig">
            <!-- Custom column for delete action -->
            <template #bodyCell="{ column, record }">
                <template v-if="column.key === 'action'">
                    <a-button type="danger" @click="handleDelete(record.id)">Delete</a-button>
                </template>
            </template>
        </a-table>

        <!-- Pagination -->
        <a-pagination v-if="totalSubmissions > 10" :current="currentPage" :total="totalSubmissions" :pageSize="10"
            @change="handlePageChange" style="margin-top: 20px;" />
    </div>
</template>

<script>
import { defineComponent, ref, onMounted, onUnmounted } from "vue";
import { db } from "@/firebase";
import { collection, onSnapshot, doc, deleteDoc } from "firebase/firestore";

export default defineComponent({
    name: "AdminPortal",
    setup() {
        // Define reactive state
        const submissions = ref([]);
        const totalSubmissions = ref(0);
        const currentPage = ref(1);
        const paginationConfig = ref({ pageSize: 10 }); // Default page size
        let unsubscribe = null; // Store snapshot listener

        // Columns configuration for Ant Design Table
        const columns = [
            { title: "Jina la Kwanza", dataIndex: "firstName", key: "firstName" },
            { title: "Jina la Mwisho", dataIndex: "lastName", key: "lastName" },
            { title: "Simu", dataIndex: "phoneNumber", key: "phoneNumber" },
            { title: "Jinsia", dataIndex: "gender", key: "gender" },
            { title: "Umri", dataIndex: "age", key: "age" },
            { title: "Mkoa", dataIndex: "region", key: "region" },
            { title: "Wilaya", dataIndex: "district", key: "district" },
            {
                title: "Tarehe ya Kutuma",
                dataIndex: "regDate",
                key: "regDate",
                render: (text) => new Date(text).toLocaleString(),
            },
            { title: "Action", key: "action" }
        ];

        // Fetch form submissions from Firestore
        const fetchSubmissions = (page = 1) => {
            try {
                if (unsubscribe) unsubscribe(); // Unsubscribe previous listener

                const startAfterDoc = (page - 1) * paginationConfig.value.pageSize;

                unsubscribe = onSnapshot(collection(db, "applications"), (querySnapshot) => {
                    const docs = querySnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));

                    // Update the state with the submissions, apply pagination
                    submissions.value = docs.slice(startAfterDoc, startAfterDoc + paginationConfig.value.pageSize);
                    totalSubmissions.value = docs.length;
                });
            } catch (error) {
                console.error("Error fetching submissions:", error);
            }
        };

        // Update all documents with a `regDate` field
        // const updateDocumentsWithRegDate = async () => {
        //     try {
        //         const querySnapshot = await getDocs(collection(db, "applications"));
        //         let baseTimestamp = Timestamp.fromDate(new Date());

        //         for (const document of querySnapshot.docs) {
        //             const randomMinutes = Math.floor(Math.random() * 5) + 1;
        //             baseTimestamp = Timestamp.fromDate(new Date(baseTimestamp.toDate().getTime() + randomMinutes * 60000));

        //             // Update document with regDate field
        //             await setDoc(doc(db, "applications", document.id), { regDate: baseTimestamp }, { merge: true });
        //             console.log(`Updated document ${document.id} with regDate`);
        //         }

        //         console.log("All documents updated with regDate");
        //     } catch (error) {
        //         console.error("Error updating documents with regDate:", error);
        //     }
        // };

        // Handle pagination page change
        const handlePageChange = (page) => {
            currentPage.value = page;
            fetchSubmissions(page);
        };

        // Handle delete action
        const handleDelete = async (id) => {
            try {
                await deleteDoc(doc(db, "applications", id)); // Delete from Firestore
                submissions.value = submissions.value.filter(submission => submission.id !== id); // Remove from state
                totalSubmissions.value -= 1;
                console.log("Submission deleted successfully!");
            } catch (error) {
                console.error("Error deleting submission:", error);
            }
        };

        // Fetch data when component is mounted
        onMounted(() => {
            fetchSubmissions(currentPage.value);
            //updateDocumentsWithRegDate();
        });

        // Unsubscribe from Firestore listener when component is unmounted
        onUnmounted(() => {
            if (unsubscribe) unsubscribe();
        });

        return {
            submissions,
            columns,
            paginationConfig,
            totalSubmissions,
            currentPage,
            handlePageChange,
            handleDelete,
        };
    },
});
</script>


<style scoped>
.admin-portal {
    margin: 20px;
}

.admin-portal h2 {
    margin-bottom: 20px;
}

.a-table {
    margin-top: 20px;
}
</style>